html {
  height: 100%;
  font-size: 62.5%;
}

body {
  font-size: 137%;
  margin: auto;
  background-color: #ff91de;
  height: 95vh;
}

.introduction {
  text-align: center;
  font-size: 3rem;
  text-transform: uppercase;
  font-family: "Goldman", cursive;
}

#container {
  padding-top: 3%;
  text-align: center;
  font-size: 2rem;
}

.copyright {
  position: absolute;
  width: 98.7%;
  height: 16px;
  bottom: 0%;
  text-align: center;
}

.pokemon {
  max-width: 100%;
  height:30%;
}
.identifier{
  size: 300%;
  font-size: 400%
};
#checkmark{
  color: lime;
}
#crossmark{
  color: red;
}

.search{
  margin: auto;
}

.pokedex-name{
  left: 2%;
  position: absolute;
  min-width: 900px; 
}

.pokedex-number{
  left: 2%;
  top: 150px;
  position: absolute;
  min-width: 900px;
}

.pokedex-pokemon{
  right: 40%;
  position: relative;
  min-width: 900px;
  height: 80%;
}

.pokemon-data-name{
  text-transform: capitalize;
}